import { useSelector } from "react-redux";
import "./Cart.css";
import { useNavigate } from "react-router-dom";
import CartItem from "./CartItem";
import { getTotalQuantity, getTotalSum } from "../../calcOnArr";
import { Divider } from "@mui/material";
import BlessingComponent from "../../components/BlessingComponent";

const Cart = () => {
  let navigate = useNavigate();

  const backToShop = () => {
    navigate('/all-products/')
  }
  const goToEndOrder = () => {
    navigate('/endOfOrder')
  }

  let ordersArr = useSelector((state) => state.order.ordersArr);
  const totalSum = getTotalSum(ordersArr);
  const totalQuantity = getTotalQuantity(ordersArr);
  return (<>  
    <h2 className="title-cart">  </h2>
    <div style={{ display: "flex", direction: "rtl" }}>

      {ordersArr.length > 0 ? (
        <table className="table-cart">
          <tr className="table-title">   
            {/* <div className="table-title"> */}
            <th className="title-item"></th>
            <th className="title-item"></th>
            <th className="title-item">"</th>
            {/* </div> */}
          </tr>

          <ul className="cart-details">
            {ordersArr.map((item) => (
              <li key={item.productCode}><CartItem oneOrder={item} /></li>
            ))}
          </ul>
        </table>) : <div className="div-empy-cart">  </div>}

      <div className="div-total">
        <BlessingComponent />
        <Divider sx={{ marginTop: "5vh", width: "16.5vw" }} />
        <h2 className="total-h2">": {totalSum} </h2>
        <h2 className="total-h2">  :{totalQuantity}</h2>
        {ordersArr.length > 0 ? (<button onClick={goToEndOrder} className="base-hover-button">  </button>) : <button onClick={backToShop} className="base-hover-button ">  </button>}
      </div>
      {/* <Divider sx={{ width: "50vw", marginLeft: "40vw" }} /> */}

    </div>
  </>);
}

export default Cart;