import * as yup from 'yup';
import React, { useState } from 'react';
import { yupResolver } from '@hookform/resolvers/yup';
import { useForm } from 'react-hook-form';
import { addUserToServer, sendMailToServer } from './userApi';
import { Alert, AlertTitle, Slide, TextField } from '@mui/material';
import { useDispatch } from 'react-redux';
import { loginState } from './userSlice';
import CustomTextField from '../../components/CustomTextField';
import LoginGoogle from './LoginGoogle';

const SignUp = () => {

  const [showAlert, setShowAlert] = useState(false);
  let dispatch = useDispatch();


  let validationSchema = yup.object().shape({
    userName: yup.string().min(3, "     3 ").required(' '),
    password: yup.string()
      .matches(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/, '         6 ')
      .required(' '),
    email: yup.string().email('   ').required(' '),
    address: yup.object().shape({
      street: yup.string().required(' '),
      city: yup.string().required(' '),
    }).required(' '),
  });

  const { register, handleSubmit, formState: { errors } } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const onSubmit = async (data) => {
    try {
      let res = await addUserToServer(data);
      console.log(data);
      dispatch(loginState(res.data))
      await sendMailToServer({ to: `${data.email}`, subject: "   ", text: `${data.userName}     ` })
      setShowAlert(true);

        setTimeout(() => {
            setShowAlert(false);

        }, 2000);
  
    } catch (err) {
      console.log(errors);
      alert(err.response.data);
    }
  };

  return (
    <>

      <Slide direction="left" in={showAlert} mountOnEnter unmountOnExit >
        <Alert severity="success" style={{ width: '20%', margin: '0 auto', textAlign: 'center',marginTop:"13vh" }}>
          <AlertTitle> </AlertTitle>
        </Alert>
      </Slide>
      <div className="login-form">
        <h2 className='form-h2' style={{ marginTop: "20vh" }}></h2>

        <h4 className='form-h4'>    </h4>
        <LoginGoogle flag='signUp' />
        <form onSubmit={handleSubmit(onSubmit)} >
          <div className="login-form">

            <CustomTextField label={''} errors={errors} register={register} field={'email'} />
            <CustomTextField label={' '} errors={errors} register={register} field={'userName'} />
            <CustomTextField label={''} errors={errors} register={register} field={'password'} />
            <CustomTextField label={''} errors={errors} register={register} field={'address.city'} defaultValue={''} />
            <CustomTextField label={''} errors={errors} register={register} field={'address.street'} defaultValue={''} />

            <button type="submit" className="base-hover-button more-hover-button" > </button>
            <h4 className='form-h4'>    ? <a className="signup-link" href='/login/'  > </a></h4>
          </div>
        </form>
      </div>
    </>
  );
};

export default SignUp;
