import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as yup from 'yup';
import { TextField, Box, FormControl, InputLabel, MenuItem, Select, FormControlLabel, Checkbox, Button, AlertTitle, Alert, Slide } from '@mui/material';
import { addProductToServer } from './productApi';
import { useSelector } from 'react-redux';
import CustomTextField from '../../components/CustomTextField';
import SaveAltIcon from '@mui/icons-material/SaveAlt';


let validationSchema = yup.object().shape({
  name: yup.string().min(3, '     3 ').required(' '),
  price: yup.number().typeError('   ').min(0, '    -0').required(' '),
  size: yup.string().min(0),
  company: yup.string().min(2, '     2 ').required(' '),
  quantityInStock: yup.number().typeError('   ').required(' '),
  color: yup.string(),
  category: yup.string(),
  imgUrl: yup.string(),
  imgUrl2: yup.string(),
  inSale: yup.boolean(),
  category: yup.string().required(' '),
  description: yup.string().required(' ')
});



const AddProductToSite = () => {

  let currentUser = useSelector(state => state.user.currentUser);
  const [showAlert, setShowAlert] = useState(false);
  const [selectedFile, setSelectedFile] = useState(null);

  const { register, handleSubmit, formState: { errors } } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const onSubmit = async (data) => {
    try {


        data.file = selectedFile;
      // alert(selectedFile.name)
      let res = await addProductToServer(data, currentUser.token)
      console.log(res);
      setShowAlert(true);
      setTimeout(() => {
        setShowAlert(false);
      }, 2000);
    }
    catch (err) {
      console.log(errors);
      alert(err.response.data);
    }
  };



  const handleFileChange = (e) => {
    setSelectedFile(e.target.files[0]);
  };



  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)} style={{ marginTop: "12vh" }}>
        <Slide direction="left" in={showAlert} mountOnEnter unmountOnExit >
          <Alert severity="success" style={{ width: '50%', margin: '0 auto', textAlign: 'center' }}>
            <AlertTitle>    </AlertTitle>
          </Alert>
        </Slide>
        <div className="login-form">
          <h2 className='form-h2'>  </h2>

          <CustomTextField label={' '} errors={errors} register={register} field={'name'} />
          <CustomTextField label={''} errors={errors} register={register} field={'price'} />
          <CustomTextField label={'/'} errors={errors} register={register} field={'size'} />
          <CustomTextField label={''} errors={errors} register={register} field={'company'} />
          <CustomTextField label={' '} errors={errors} register={register} field={'quantityInStock'} />
          <CustomTextField label={''} errors={errors} register={register} field={'color'} />
          <CustomTextField label={' '} errors={errors} register={register} field={'description'} />
          <CustomTextField label={''} errors={errors} register={register} field={'category'} />


          {/*                 .         */}
          {/*            ,            */}
          {/*             ,         */}
          {/*   .     ,             ,       . */}
          {/*         .                 *     . */}
          {/*    .            .            . */}
          {/*                    */}


          {/* <FormControl variant="filled" sx={{ width: "25vw", marginBottom: "15px" }}>
            <InputLabel id="demo-simple-select-filled-label"></InputLabel>
            <Select
              labelId="demo-simple-select-filled-label"
              id="demo-simple-select-filled"
              value={age}
              onChange={handleChange}
              sx={{
                backgroundColor: "white",
                border: "1px solid",
                color: "black",
                height: "5.5vh", // Set height to auto to adjust based on content
                "& .MuiSelect-root": {
                  height: "2rem", // Adjust the height of the Select component
                  lineHeight: "2rem", // Center the text vertically
                },
                "& .MuiInputBase-root": {
                  height: "2rem", // Adjust the height of the input field
                },
                "& p": {
                  color: "red",
                },
              }}
            >
              <MenuItem value="">
                <em></em>
              </MenuItem>
              <MenuItem value={10}> </MenuItem>
              <MenuItem value={20}></MenuItem>
              <MenuItem value={30}></MenuItem>
            </Select>
          </FormControl> */}

          <div>
            <input
              type="file"
              accept="image/*"
              style={{ display: 'none' }}
              id="fileInput"
              onChange={handleFileChange}

            />
            <label htmlFor="fileInput">
              <Button sx={{
                backgroundColor: "white",
                color: "black",
                border: "1px solid", marginBottom: "10px",
                '&:hover': {
                  backgroundColor: "white",
                  color: "black"
                }
              }}
                variant="contained"
                component="span"
                startIcon={<SaveAltIcon />}
              >
                 
              </Button>
            </label>
          </div>

          <FormControlLabel control={<Checkbox />}    {...register('inSale')} label="" />
          {/*    */}
          <button className='base-hover-button more-hover-button' type="submit" > </button>
        </div>
      </form>
    </>
  );
};

export default AddProductToSite;














